/* purgecss start ignore */

.lc-image-cropper-modal {
    position: relative;
  }

.lc-image-cropper-modal .MuiCard-root {
      overflow-y: scroll;
      overflow: auto;
      max-height: calc(100vh - 40px);
      max-width: calc(100vw - 40px);
    }

.lc-image-cropper-modal .MuiCardContent-root {
      padding-top: 0 !important;
      padding-bottom: 0 !important;
    }

.lc-image-cropper-header {
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-left: -16px;
    margin-right: -16px;
    margin-bottom: 0;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
    width: 100%;
    box-sizing: content-box;
    z-index: 10;
  }

.lc-image-cropper-cropper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 24px;
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-gray-lightest), var(--tw-bg-opacity));
    border-radius: 4px;
    background-size: 20px 20px;
    background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
  }

.lc-image-cropper-cropper .ReactCrop {
      margin: 0 !important;
    }

.lc-image-cropper-preview {
    display: flex;
    align-items: center;
  }

.lc-image-cropper-preview-image {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 48px;
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-gray-lighter), var(--tw-bg-opacity));
      border-radius: 4px;
      margin-right: 8px;
    }

.lc-image-cropper-preview-image img {
        width: 40px;
        height: 40px;
        object-fit: contain;
      }

.lc-image-cropper-footer {
    display: flex;
    align-items: center;
    padding: 24px;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
    width: 100%;
    margin-left: -24px;
    margin-right: -24px;
    box-sizing: content-box;
  }

.lc-image-cropper-actions {
    margin: -4px;
  }

.lc-image-cropper-actions .lc-button {
      margin: 4px;
    }

/* purgecss end ignore */
